.nfd-coming-soon-content {
    display: flex;
    flex-direction: column;
    gap: 1rem;

    @media (min-width: 782px) {
        &.md\:nfd-flex-row-reverse {
            flex-direction: row-reverse !important;
        }
    }
}

#iframe-preview-wrap {
    position: relative;
    overflow: hidden;
    padding-top: 60%;
    border: 1px solid #dbd1d1;
    border-radius: 0.375rem;
    border: 1px solid #ECECEC;
    width: 100%;
}

#iframe-preview-label {
    width: 100%;
    position: absolute;
    top: 0;
    text-transform: uppercase;
}

#iframe-preview-detail {
    width: 100%;
    position: absolute;
    bottom: 0;
}

#iframe-wrap {
    position: relative;
}

#iframe-preview {
    width: 400%;
    height: 400%;
    transform: scale(0.25);
    transform-origin: top left;
    position: absolute;
    top: 20px;
    /* offset for label and iframe adminbar */
    left: 0;
    right: 0;
    bottom: 0;
}

.iframe-preview-domain {
    font-weight: bold;
}

.iframe-preview-status svg {
    width: 1.5rem;
}

.iframe-preview-status.status-coming-soon {
    color: rgb(205 0 1);
}

.iframe-preview-status.status-live {
    color: rgb(0 109 18);
}